.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}
.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3__navbar-links_logo {
  margin-right: 2rem;
}
.gpt3__navbar-links_logo img {
  width: 62px;
  height: 16px;
}
.gpt3__navbar-links ul {
  display: flex;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.gpt3__navbar li,
.gpt3__navbar-sign p,
.gpt3__navbar-menu-sign p,
.gpt3__navbar-menu-sign button,
.gpt3__navbar-sign button {
  color: #fff;
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}
.gpt3__navbar-menu-sign button,
.gpt3__navbar-sign button {
  padding: 0.5rem 1rem;
  left: 1153px;
  top: 55px;
  background: #ff4820;
  border-radius: 5px;
  border: 0;
}
.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}
.gpt3__navbar-menu svg {
  cursor: pointer;
}
.gpt3__navbar-menu_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  text-align: end;
  padding: 2rem;
  background-color: var(--color-footer);
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu_container li {
  margin: 0.5rem 0;
}
.gpt3__navbar-sign p:hover,
.gpt3__navbar li a,
.gpt3__navbar-menu_container li a {
  transition: 0.5s;
}
.gpt3__navbar-sign p:hover,
.gpt3__navbar li a:hover,
.gpt3__navbar-menu_container li a:hover {
  color: #ff4820;
}
.gpt3__navbar-menu-sign {
  display: none;
}
@media screen and (max-width: 1075px) {
  .gpt3__navbar-links ul {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
}
@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign {
    display: none;
  }
  .gpt3__navbar-menu_container {
    top: 20px;
  }
  .gpt3__navbar-menu-sign p,
  .gpt3__navbar-menu-sign button {
    margin: 0.5rem 0;
  }
  .gpt3__navbar-menu-sign {
    display: block;
  }
}
